export default {
	data() {
		return {
			persent:'',
			n:true,
			number:'',
			id:'',
			question:'',
			poolDetail:'',
			month:false,
			year:false,
			nums: '1' ,
			tabCurrentIndex: 0,
			navList: [
				{
					state: 0,
					text: '矿池介绍'
				},
				{
					state: 1,
					text: '合约详情'
				},
				{
					state: 2,
					text: '常见问题'
				}
			]
		};
	},
	onLoad(option) {
		this.id=option.cloudid;
		uni.request({
			url: this.url + 'cloudmessage/',
			method: 'GET',
			data: {cloudid:this.id},
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success: res => {
				console.log(res);
				this.poolDetail=res.data.data;
				this.persent=parseFloat(this.poolDetail.harduse/this.poolDetail.hardfree)*100
				console.log(parseInt(this.persent))
			}
		});
		uni.request({
			url: this.url + 'cloudask/',
			method: 'GET',
			header: {
				Authorization: 'JWT' + ' ' + this.global_.token
			},
			success: res => {
				console.log(res.data.data);
				this.question=res.data.data;
			}
		});
		
	},
	methods: {
		next:function(){
			this.n=false
		},
		back:function(){
		   this.n=true
		},
		changeTab(e) {
			this.tabCurrentIndex = e.target.current;
		},
		tabClick(index) {
			var that=this;
			that.tabCurrentIndex = index;
		},
		buynow:function(){
			uni.showToast({
				title:'预购即将开启',
				icon:'none',
				duration:3000
			})
			// console.log(this.poolDetail)
			// var a=JSON.stringify(this.poolDetail)
			// console.log(a)
			// uni.navigateTo({
			// 	url: '../../my/submit/submit?a='+a,
			// 	success: res => {},
			// 	fail: () => {},
			// 	complete: () => {}
			// });
		},
		buy_month:function(){
			this.month=true;
			this.year=false;
		},
		buy_year:function(){
			this.year=true;
			this.month=false;
		}
	}
};
